"use client";

import { useEffect, useState } from "react";
import { useParams } from "next/navigation";
import api from "@/lib/axios";
import { ClipboardList } from "lucide-react";

export default function ManagerOrdersPage() {
  const { restaurantId } = useParams();
  const [orders, setOrders] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchOrders = async () => {
      try {
        const res = await api.get(`/orders/manager`);
        setOrders(res.data);
      } catch (err) {
        console.error("Failed to load orders", err);
      } finally {
        setLoading(false);
      }
    };

    if (restaurantId) fetchOrders();
  }, [restaurantId]);

  if (loading) return <p className="p-10 text-xl">Loading orders...</p>;

  return (
    <div className="max-w-6xl mx-auto p-6">
      <h1 className="text-4xl font-bold text-black mb-8 flex items-center gap-3">
        <ClipboardList className="text-orange-600 w-8 h-8" />
        Restaurant Orders
      </h1>

      {orders.length === 0 ? (
        <p className="text-gray-600">No orders found for this restaurant.</p>
      ) : (
        <div className="space-y-6">
          {orders.map((order) => (
            <div
              key={order.id}
              className="bg-white border rounded-2xl shadow-md p-6"
            >
              {/* Order Header */}
              <div className="flex justify-between items-center mb-4">
                <div>
                  <p className="text-black font-semibold">
                    Order ID: {order.id}
                  </p>
                  <p className="text-gray-600 text-sm">
                    Placed on: {new Date(order.createdAt).toLocaleString()}
                  </p>
                </div>

                <span
                  className={`px-4 py-1 rounded-full text-sm font-semibold ${
                    order.status === "confirmed"
                      ? "bg-green-100 text-green-700"
                      : "bg-yellow-100 text-yellow-700"
                  }`}
                >
                  {order.status.toUpperCase()}
                </span>
              </div>

              {/* Customer */}
              <p className="text-black mb-3">
                <strong>Customer:</strong> {order.user?.name}
              </p>

              {/* Order Items */}
              <div className="bg-gray-100 rounded-xl p-4">
                <p className="font-semibold text-black mb-2">Items</p>

                {order.items.map((item: any, idx: number) => (
                  <div
                    key={idx}
                    className="flex justify-between text-gray-800 mb-1"
                  >
                    <span>
                      {item.menuItem.name} × {item.quantity}
                    </span>
                    <span>
                      ₹ {item.menuItem.price * item.quantity}
                    </span>
                  </div>
                ))}
              </div>

              {/* Total */}
              <div className="flex justify-end mt-4">
                <p className="text-xl font-bold text-black">
                  Total: ₹ {order.totalPrice}
                </p>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
